.footer-container {
  background-color: #F7B520;
  font-size: 15px;
  padding-left: 40px;
  padding-right: 40px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  font-weight: 500;
  padding-top: 10px;
}
.footer-container .left-col {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.footer-container .left-col ul {
  display: -ms-flexbox;
  display: flex;
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
}
.footer-container .right-col {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
}
.footer-container .right-col p {
  color: rgb(34, 34, 34);
  margin-top: 10px;
}

@media (max-width: 480px) {
  .footer-container {
    display: block;
  }
  .footer-container .left-col {
    -ms-flex-direction: column;
        flex-direction: column;
    margin-bottom: 5px;
  }
  .footer-container .right-col {
    display: block;
    text-align: center;
  }
}


